import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { com.pushpak_gosavi.pushpak.pages.homePage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(com.pushpak_gosavi.pushpak.style.AboutImageStyle)
        ctx.theme.registerComponentStyle(com.pushpak_gosavi.pushpak.style.AboutSectionText)
        ctx.theme.registerComponentStyle(com.pushpak_gosavi.pushpak.style.BackToTopStyle)
        ctx.theme.registerComponentStyle(com.pushpak_gosavi.pushpak.style.CertificateLeftArrowStyle)
        ctx.theme.registerComponentStyle(com.pushpak_gosavi.pushpak.style.CertificateRightArrowStyle)
        ctx.theme.registerComponentStyle(com.pushpak_gosavi.pushpak.style.InputTypeStyle)
        ctx.theme.registerComponentStyle(com.pushpak_gosavi.pushpak.style.NavigationItemStyle)
        ctx.theme.registerComponentStyle(com.pushpak_gosavi.pushpak.style.LogoStyle)
        ctx.theme.registerComponentStyle(com.pushpak_gosavi.pushpak.style.SocialLinksStyle)
        ctx.theme.registerComponentStyle(com.pushpak_gosavi.pushpak.style.MainButtonStyle)
        ctx.theme.registerComponentStyle(com.pushpak_gosavi.pushpak.style.MainImageStyle)
        ctx.theme.registerComponentStyle(com.pushpak_gosavi.pushpak.style.PortfolioSectionStyle)
        ctx.theme.registerComponentStyle(com.pushpak_gosavi.pushpak.style.ArrowStyle)
        ctx.theme.registerComponentStyle(com.pushpak_gosavi.pushpak.style.ServiceCardStyle)
        com.pushpak_gosavi.pushpak.updateTheme(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin))

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(
            AppGlobalsLocal provides mapOf("title" to "Pushpak Gosavi")
        ) { com.pushpak_gosavi.pushpak.MyApp {
              router.renderActivePage { renderWithDeferred { it() } }
        } }
    }
}
